/* Alert Component
 * * @description  Create a .alert classes
 * * @package      components
 * * @version      1.0.0
 * * @lastupdate   31 May 2020 */
.alert {
  align-items: center;
  background-color: #e6e6e6;
  border-radius: 4px;
  color: #333;
  cursor: default;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
  padding: 12px;
  position: relative;
  transition: background-color 200ms ease-in, color 200ms ease-in;
}
.alert + .alert {
  margin-top: 12px;
}
.alert:hover {
  background-color: #ccc;
  border-color: #ccc;
  text-decoration: none;
}
.alert .alert__content {
  flex: 1;
}
.alert .alert__icon {
  font-size: 20px;
  line-height: 1;
  width: 40px;
}
.alert .alert__close {
  background-color: transparent;
  border-width: 0;
  box-shadow: none;
  content: "×";
  cursor: pointer;
  font-size: 20px;
  font-weight: 600;
  line-height: 1;
  margin: 0 0 0 12px;
  outline: none;
  padding: 0;
}
.alert.alert--is-default {
  background-color: #e6e6e6;
  border-color: #e6e6e6;
  color: #333;
}
.alert.alert--is-default:hover {
  background-color: #ccc;
  border-color: #ccc;
}
.alert.alert--is-primary {
  background-color: #F66A26;
  border-color: #F66A26;
  color: #fff;
}
.alert.alert--is-primary:hover {
  background-color: #dd3906;
  border-color: #dd3906;
}
.alert.alert--is-secondary {
  background-color: #1b78cd;
  border-color: #1b78cd;
  color: #fff;
}
.alert.alert--is-secondary:hover {
  background-color: #004abe;
  border-color: #004abe;
}
.alert.alert--is-danger {
  background-color: #d91620;
  border-color: #d91620;
  color: #fff;
}
.alert.alert--is-danger:hover {
  background-color: #990b13;
  border-color: #990b13;
}
.alert.alert--is-success {
  background-color: #207f33;
  border-color: #207f33;
  color: #fff;
}
.alert.alert--is-success:hover {
  background-color: #0b2e12;
  border-color: #0b2e12;
}
.alert.alert--is-white {
  background-color: #fff;
  border-color: #fff;
  color: #333;
}
.alert.alert--is-white:hover {
  background-color: #f9f9f9;
  border-color: #f9f9f9;
}
.alert.alert--is-dark {
  background-color: #1f1f1f;
  border-color: #1f1f1f;
